import { handleResponse, profile } from '@ab/framework';

var graphql = (accountNumber) => JSON.stringify({
  query: " query findOne($accountNumber: Int) {  findOne(accountNumber: $accountNumber) {    accountNumber    balances {      outstandingBalance      currentBalance    }  }}",
  variables: { accountNumber }
})

export const getBalance = async (signal) => {
	let requestUrl = `https://accounts-service-gqhl-aka2bqzvrq-em.a.run.app/graphql`;
	try {
		let response = await fetch(requestUrl, {
			signal,
			body: graphql(profile('accountNumber') || 199432), // TODO: remove hardcode here. This shud come from bridge
			method: "POST",
			headers: {
				"Content-Type": "application/json"
			},
		});
		let result = await handleResponse(response);
		return result.data.findOne?.balances?.currentBalance;
	} catch (e) {
		throw e;
	}
};

