import * as React from 'react';
import { getBalance } from './service';
import { formatNumber } from '@ab/common';
import { isMobile, useFetch, useGps, useEventListener, navigateToUrl, constants } from '@ab/framework';
import {
  Grid, Card, CardActions, CardContent, Button, Typography, Skeleton
} from 'material-ui';

export default function UI({ type }) {

  const [amount, setAmout] = React.useState(2);
  const [location, setLocation] = React.useState('');

  useEventListener('CARD_APPLIED', '@ab/w-card-spend', (data, err) => {
    setAmout(amount => amount + 1)
  });

  useEventListener(constants.BRIDGE_OPERATIONS.GPS_LOCATION, process.env.HELIOS_WEB_PROJECT_NAME, (data, err) => {
    setLocation(JSON.stringify(data))
  });

  const [loading] = useFetch(type !== 'card' ? getBalance : null, {}, (data, error) => {
    if (error) {

    } else {
      setAmout(data)
    }
  }, [])

  
  const onView = () => {
    if (isMobile()) {
      useGps(process.env.HELIOS_WEB_PROJECT_NAME, process.env.HELIOS_WEB_VERSION)
    } else {
      navigateToUrl('balance')
    }
  }

  return (
    <Card raised>
      <CardContent>
        <Grid container>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {type === 'account' ? 'Deposit' : 'View Balance'}
          </Typography>
        </Grid>
        <Grid container justifyContent="space-between">
          <Grid item></Grid>
          <Grid item>
            <Typography align="right" variant="h5" component="div">
              {type === 'account' ? '3.23 JOD' : formatNumber(amount, 'JOD')}
            </Typography>
            {loading && <Skeleton variant="text" sx={{ fontSize: '1rem' }} />}
            {!loading && <Typography align="right" sx={{ mb: 1.5 }} color="text.secondary">
              Account Balance {location}
            </Typography>}
          </Grid>
        </Grid>
      </CardContent>
      <CardActions style={{ background: type === 'account' ? '#ce6a6b' : type === 'card' ? '#4ebe79' : '#679ed4' }}>
        <Grid container justifyContent="flex-end">
          <Button variant='contained' size="small" style={{ background: 'white', color: 'grey' }} onClick={onView}>
            <svg xmlns="http://www.w3.org/2000/svg" width="15px" height="15px" viewBox="0 0 576 512">
              <path fill="#888" d="M572.52 241.4C518.29 135.59 410.93 64 288 64S57.68 135.64 3.48 241.41a32.35 32.35 0 0 0 0 29.19C57.71 376.41 165.07 448 288 448s230.32-71.64 284.52-177.41a32.35 32.35 0 0 0 0-29.19zM288 400a144 144 0 1 1 144-144 143.93 143.93 0 0 1-144 144zm0-240a95.31 95.31 0 0 0-25.31 3.79 47.85 47.85 0 0 1-66.9 66.9A95.78 95.78 0 1 0 288 160z"/>
            </svg>
            &nbsp;View 
          </Button>
        </Grid>
      </CardActions>
    </Card>
  );
}
